import React from "react";
import { createRoot } from "react-dom/client";

const loadCustomCSS = () => import("./custom-styles.css");

const defaultInputTextPromise = import("./defaultInputText.md");

const Editor = React.lazy(() => import("./editor"));
const Preview = React.lazy(() => import("./preview"));
const Navbar = React.lazy(() => import("./navbar"));

class MarkdownPreviewer extends React.Component {
  constructor(props) {
    super(props);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleViewToggle = this.handleViewToggle.bind(this);
    this.state = {
      text: "",
      isVerticalOnTablet: true,
    };
  }

  showLoadingIncidatorText() {
    this.setState({ text: "Loading..." });
  }

  fetchAndSetDefaultInputText() {
    // Use import because defaultInputText.md is static and will not change during runtime
    defaultInputTextPromise
      .then((module) => {
        this.setState({ text: module.default });
        console.log("Default input text loaded and set.");
      })
      .catch((error) => {
        // this.setState({ text: error });
        console.error(error);
      });
  }

  handleInputChange(inputTextValue) {
    this.setState({
      text: inputTextValue,
    });
  }

  handleViewToggle() {
    this.setState({ isVerticalOnTablet: !this.state.isVerticalOnTablet });
  }

  componentDidMount() {
    this.showLoadingIncidatorText();
    Promise.all([loadCustomCSS()]).then(() => {
      console.log("CSS files loaded");
    });
    this.fetchAndSetDefaultInputText();
  }

  render() {
    const isVerticalOnTablet = this.state.isVerticalOnTablet;
    const text = this.state.text;

    return (
      <div>
        <React.Suspense fallback={<div>Loading...</div>}>
          <Navbar
            isVerticalOnTablet={isVerticalOnTablet}
            handleViewToggle={this.handleViewToggle}
          />
        </React.Suspense>
        <section className="section">
          <div className="columns is-multiline is-gapless">
            <React.Suspense fallback={<div>Loading...</div>}>
              <Editor
                defaultTextValue={text} // ran only once during startup
                onInputChange={this.handleInputChange}
                isVerticalOnTablet={isVerticalOnTablet}
              />
            </React.Suspense>
            <React.Suspense fallback={<div>Loading...</div>}>
              <Preview isVerticalOnTablet={isVerticalOnTablet} text={text} />
            </React.Suspense>
          </div>
          <div className="has-text-centered"></div>
        </section>
      </div>
    );
  }
}

// ========================================

const root = createRoot(document.getElementById("root"));
root.render(<MarkdownPreviewer />);
